import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {ViewController} from 'ionic-angular/navigation/view-controller';
import {AlertController} from 'ionic-angular/components/alert/alert-controller';
import {ContentProvider} from '../../providers/content/content';

@IonicPage()
@Component({
  selector: 'page-message-creator',
  templateUrl: 'message-creator.html'
})
export class MessageCreatorPage {
  arrangements: any = [];
  postData: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private alertCtrl: AlertController,
    private cntProvider: ContentProvider
  ) {
    this.postData = navParams.get('postData');
    this.cntProvider.getArrangements().then(data => {
      this.arrangements = data;
    });
  }

  close(): void {
    this.viewCtrl.dismiss();
  }

  saveChanges(): void {
    this.viewCtrl.dismiss(this.postData);
  }

  delete(): void {
    const alert = this.alertCtrl.create({
      title: 'Delete message?',
      message: 'Er du helt sikker?',
      buttons: [
        {
          text: 'Behold',
          handler: () => {
            return;
          }
        },
        {
          text: 'Slett',
          handler: () => {
            this.postData.delete = true;
            this.viewCtrl.dismiss(this.postData);
            return;
          }
        }
      ]
    });
    alert.present();
  }
}



